

.disabled {
    pointer-events: none;
    opacity: 0.7;
}

.my-widget {
    margin-bottom: 20px;
}

.not-found-result {
    margin-top: 60px;
    .message {
        background-color: rgba(#ffc107, 0.4);
        color: #333;
        font-weight: 600;
        font-size: 18px;
        padding: 30px;
        text-align: center;
        border-radius: 6px;
    }
}

.my-row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .my-col {
        width: 100%;
        max-width: 100%;
        flex: 1 0 0%;
        padding: 0 10px;
    }
}


.space-between {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}


.logo-center {
    width: 100%;
    display: flex;
    justify-content: center;
}


.no-posts {
    padding: 50px;
    background-color: #FFF;
    color: #666;
    border: 1px solid #DDD;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    box-shadow: 0 .5rem 1.5rem .5rem rgba(0,0,0,.075)!important;
    margin: {
        top: 40px;
        bottom: 40px;
    };
    &.hide {
        display: none;
    }
}


.articles-box-title .articles-box-filter-links li {
    &.active a {
        color: #105EFB !important;
    }
}